// app/countries/loading.tsx
'use client';

export default function Loading() {
  // Adjust these as needed:
  const NAVBAR_HEIGHT = 100;      // height of your navbar
  const TABLE_COLUMNS = 9;        // number of columns in your final table
  const SKELETON_ROWS = 5;        // number of skeleton rows for the table

  const columns = Array.from({ length: TABLE_COLUMNS }, (_, i) => i);
  const rows = Array.from({ length: SKELETON_ROWS }, (_, i) => i);

  return (
    <div
      className={`min-h-screen animate-pulse px-8 pt-[${NAVBAR_HEIGHT}px] pb-10 flex flex-col items-center bg-gray-50`}
    >
      <div className="w-full max-w-7xl space-y-6">
        {/* Title Skeleton */}
        <div className="h-8 bg-gray-300 rounded w-1/2 mx-auto"></div>

        {/* Filter Form Skeleton */}
        <div className="space-y-4">
          {/* Filter row 1 */}
          <div className="h-10 bg-gray-200 rounded w-full"></div>
          {/* Filter row 2 */}
          <div className="h-10 bg-gray-200 rounded w-full"></div>
        </div>

        {/* Table Skeleton */}
        <div className="overflow-x-auto">
          <table className="min-w-full border border-gray-200">
            <thead className="bg-gray-200">
            <tr>
              {columns.map((col) => (
                <th key={col} className="p-2 border">
                  <div className="h-4 bg-gray-300 rounded mx-auto w-3/4"></div>
                </th>
              ))}
            </tr>
            </thead>
            <tbody>
            {rows.map((row) => (
              <tr key={row} className="text-center">
                {columns.map((col) => (
                  <td key={col} className="p-2 border">
                    <div className="h-4 bg-gray-300 rounded mx-auto w-3/4"></div>
                  </td>
                ))}
              </tr>
            ))}
            </tbody>
          </table>
        </div>

        {/* Pagination Skeleton */}
        <div className="h-8 bg-gray-300 rounded w-1/3 mx-auto"></div>
      </div>
    </div>
  );
}
